import React from 'react';
import Typography from '@material-ui/core/Typography';
import Layout from '../layout/Layout';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Metadata from '../components/Metadata';
import { ContactPageMetadata } from '../configs/PageMetadata';

const useStyles = makeStyles(() => ({
  root: {
    margin: 20,
    padding: 20
  },
  sectionTitle: {
    paddingBottom: 10,
    fontSize: '1.20rem'
  }
}));

export default function ContactPage() {
  const classes = useStyles();
  return (
    <Layout>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          Contact
      </Typography>
        <Typography paragraph>
          You can connect with me for scheduling a guest lecture at your institution. If you are a technology enthusiast too, please feel free to network with me on LinkedIn. You can also get technical consulting from me on Digital Transformation for any good social cause.
        </Typography>
        <Typography paragraph>
          Please drop an email by <a href="mailto:email@bipul.in">clicking here.</a>
        </Typography>
      </Paper>
      <Metadata
        Title={ContactPageMetadata.Title}
        Description={ContactPageMetadata.Description}
      />
    </Layout>
  );
}